import React, { useState } from 'react';
import { Route, Routes, NavLink } from 'react-router-dom'; // version 6 ::  
import {
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    //     UserOutlined,
    //     FileOutlined,
    //     PieChartOutlined,
    //     TeamOutlined,
} from '@ant-design/icons';
import { Layout, Button, theme } from 'antd';
import logo from './logo.svg';
import './App.css';
import { Home, Notice, MyPage, Basic, Diary, ToDoList, SamplePage } from './components';

const { Header, Content, Sider } = Layout;

const menuItems = [
    { url: '/', name: '', component: Home },
    { url: '/Basic', name: '', component: Basic },
    { url: '/Notice', name: '', component: Notice },
    { url: '/Diary', name: '', component: Diary },
    { url: '/ToDoList', name: '', component: ToDoList },
    { url: '/SamplePage', name: '', component: SamplePage },
    { url: '/MyPage', name: '', component: MyPage },

    //   
];

//    
function App() {
    /* package.json  "homepage": "https://seolhwa-y.github.io/react-app",  */

    const [collapsed, setCollapsed] = useState(false);
    const {
        token: { colorBgContainer, borderRadiusLG },
    } = theme.useToken();

    return (
        <Layout>
            {/*    */}
            <Sider trigger={null} collapsible collapsed={collapsed}>
                <div className="demo-logo-vertical" />
                {/* <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline" items={items} /> */}

                {/*    */}
                {menuItems.map((menuItem, index) => (
                    <p key={index}>
                        <NavLink to={menuItem.url}>{menuItem.name}</NavLink>
                    </p>
                ))}
            </Sider>

            <Layout>
                {/*  */}
                <Header
                    style={{
                        padding: 0,
                        background: colorBgContainer,
                    }}>
                    <Button
                        type="text"
                        icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
                        onClick={() => setCollapsed(!collapsed)}
                        style={{
                            fontSize: '16px',
                            width: 64,
                            height: 64,
                        }}
                    />
                </Header>

                {/*  */}
                <Content
                    style={{
                        margin: '24px 16px',
                        padding: 24,
                        minHeight: 280,
                        background: colorBgContainer,
                        borderRadius: borderRadiusLG,
                    }}>
                    <img src={logo} className="App-logo" alt="logo" />

                    {/*  version 6 */}
                    <Routes>
                        {menuItems.map((menuItem, index) => (
                            <Route
                                key={index}
                                path={menuItem.url}
                                // component={<menuItem.component />} // version 6 
                                element={<menuItem.component />} // version 6
                            />
                        ))}
                    </Routes>
                </Content>
            </Layout>
        </Layout>
    );
}

export default App;
