import { React, useState } from 'react';

//  - HEADER
function Header(props) {
    console.table(props);

    return (
        <header /*className="App-header"*/>
            <h1 className="title">
                <a
                    href="/"
                    onClick={(event) => {
                        event.preventDefault(); //   
                        props.onChangeMode();
                    }}>
                    {props.title}
                </a>
            </h1>
        </header>
    );
}

//  - LIST
function Nav(props) {
    console.table(props);

    const lis = [];

    //     key . for  map   
    for (let i = 0; i < props.topics.length; i++) {
        let cont = props.topics[i];

        lis.push(
            <li key={cont.id}>
                <a
                    id={cont.id} // id alert   id 
                    href={'/01/' + cont.id}
                    onClick={(event) => {
                        event.preventDefault();
                        props.onChangeMode(Number(event.target.id)); //   id  
                    }}>
                    {cont.title}
                </a>
            </li>
        );
    }

    return (
        <nav>
            <ol>{lis}</ol>
        </nav>
    );
}

//  - DETAIL
function Article(props) {
    console.table(props);

    const title = props.title;
    const body = props.body;

    return (
        <article>
            <h2>{title}</h2> {body}
        </article>
    );
}
//  - UPDATE
function Update(props) {
    // props =>  => 
    // state =>  => 
    console.table('Update', props);
    const [title, setTitle] = useState(props.title);
    const [body, setBody] = useState(props.body);

    return (
        <article>
            <h3></h3>
            <form
                onSubmit={(event) => {
                    const _title = event.target.title.value;
                    const _body = event.target.body.value;
                    props.onUpdate(_title, _body);
                }}>
                <p>
                    <input
                        type="text"
                        name="title"
                        placeholder="title"
                        value={title}
                        onChange={(event) => {
                            setTitle(event.target.value);
                        }}
                    />
                </p>
                <p>
                    <textarea
                        name="body"
                        placeholder="body"
                        value={body}
                        onChange={(event) => {
                            setBody(event.target.value);
                        }}
                    />
                </p>
                <p>
                    <input type="submit" value="Update" />
                </p>
            </form>
        </article>
    );
}

//  - CREATE
function Create(props) {
    return (
        <article>
            <h3></h3>
            <form
                onSubmit={(event) => {
                    event.preventDefault();

                    const title = event.target.title.value;
                    const body = event.target.body.value;
                    props.onCreate(title, body);
                }}>
                <p>
                    <input type="text" name="title" placeholder="title" />
                </p>
                <p>
                    <textarea name="body" placeholder="body" />
                </p>
                <p>
                    <input type="submit" value="Create" />
                </p>
            </form>
        </article>
    );
}

//    
function Basic() {
    const [mode, setMode] = useState('WELCOME'); // State  (mode)  (welcode)
    const [id, setId] = useState(null);
    const [nextId, setNextId] = useState(4);
    const [topics, setTopics] = useState([
        { id: 1, title: 'html', body: 'html is ...' },
        { id: 2, title: 'js', body: 'js is ...' },
        { id: 3, title: 'css', body: 'css is ...' },
    ]); // props   
    let contents = '';
    let contextControl = null;
    let title,
        body = null;

    if (mode === 'WELCOME') {
        contents = <Article title="" body="  !!"></Article>;
    } else if (mode === 'READ') {
        for (let i = 0; i < topics.length; i++) {
            if (topics[i].id === id) {
                title = topics[i].title;
                body = topics[i].body;
            }
        }
        contents = <Article title={title} body={body}></Article>;
        contextControl = (
            <>
                <li>
                    <a
                        href={'/update/' + id}
                        onClick={(event) => {
                            event.preventDefault();
                            setMode('UPDATE');
                        }}>
                        
                    </a>
                </li>
                <li>
                    <input
                        type="button"
                        value="delete"
                        onClick={() => {
                            const newTopics = [];

                            for (let i = 0; i < topics.length; i++) {
                                if (topics[i].id !== id) {
                                    newTopics.push(topics[i]);
                                }
                            }

                            setTopics(newTopics);
                            setMode('WELCOME');
                        }}
                    />
                </li>
            </>
        );
    } else if (mode === 'CREATE') {
        contents = (
            <Create
                onCreate={(_title, _body) => {
                    console.log(_title, _body);

                    const newTopics = [...topics];
                    const newTopic = { id: nextId, title: _title, body: _body };

                    newTopics.push(newTopic);
                    setTopics(newTopics);
                    setMode('READ');
                    setId(nextId);
                    setNextId(nextId + 1);
                }}></Create>
        );
    } else if (mode === 'UPDATE') {
        for (let i = 0; i < topics.length; i++) {
            if (topics[i].id === id) {
                title = topics[i].title;
                body = topics[i].body;
                break;
            }
        }

        contents = (
            <Update
                onUpdate={(_title, _body) => {
                    console.log('onUpdate', _title, _body);

                    const updTopic = { id: id, title: _title, body: _body };

                    for (let i = 0; i < topics.length; i++) {
                        if (topics[i].id === updTopic.id) {
                            topics[i] = updTopic;
                            break;
                        }
                    }

                    setMode('READ');
                    setId(id);
                }}
                title={title}
                body={body}></Update>
        );
    }

    return (
        <div>
            {/*
              --  React 
              1.   :   header ,    
              2.  PROPS      : title='' => param props => {props.title} =>   
              3.       :              .
              4. State           :  [(), (function)] = useState('');
              5. Create          : 
              6. Update          :
              7. Delete          :
                    // const [value, setValue] = useState(PRIMITIVE); -- string, number, bigint, boolean, undefined, symbol, null
                    // const [value, setValue] = useState(OBJECT); -- object, array
                    // newValue = {...value} newValue = [...value]

             */}

            <Header
                title="REACT"
                onChangeMode={() => {
                    setMode('WELCOME');
                    // alert('danger!');
                }}></Header>
            <Nav
                topics={topics}
                onChangeMode={(_id) => {
                    // alert(_id);
                    setId(_id);
                    setMode('READ');
                }}></Nav>
            {contents}

            <li>
                <a
                    href="/create"
                    onClick={(event) => {
                        event.preventDefault();
                        setMode('CREATE');
                    }}>
                    
                </a>
            </li>
            {contextControl}
        </div>
    );
}

export default Basic;
